
import CHeader from '@/components/x-header'
import XButton from 'vux/src/components/x-button/index.vue'

import { go } from '@/libs/router'
export default {
components: {
XButton,
CHeader
},
created() {
if (this.$route.query && this.$route.query.token && this.$route.query.token.length > 0) {
let tk = this.$route.query.token
this.$LStorage.setItem('mr_token', tk)
} else {
let message = { backClick: 0, logOut: 1 }
this.$LStorage.removeItem('mr_token')
window.parent.postMessage(JSON.stringify(message), '*')
}
},
methods: {
goBackHome() {
let message = { backClick: 1, logOut: 0 }
window.parent.postMessage(JSON.stringify(message), '*')
}
}
}



